import React from "react"
import SEO from "../components/seo"

import Layout from "../components/layout"

import aboutimg from "../images/gmlunesa-pic.jpg"

import Typing from "react-typing-animation"

const AboutPage = () => (
  <Layout>
    <SEO title="About" />
    {/* <Masthead /> */}
    <section className="about">
      <div className="container h-100">
        <div className="row mt-4 justify-content-center">
          <div className="col-lg-3">
            <div className="img-container">
              <img src={aboutimg} />
            </div>
          </div>
          <div className="col-lg-7 my-auto headline">
            <h1>If I wasn't a software engineer, I'd like to be...</h1>
            <Typing loop cursorClassName={"cursor"}>
              <h2>
                An astronaut!
                <Typing.Backspace count={20} delay={1000} />
                <Typing.Delay ms={200} />
                An astronomer.
                <Typing.Backspace count={20} delay={1000} />
                <Typing.Delay ms={200} />
                A meteorologist.
                <Typing.Backspace count={20} delay={1000} />
                <Typing.Delay ms={200} />
                A volcanologist.
                <Typing.Backspace count={20} delay={1000} />
                <Typing.Delay ms={200} />
                A math teacher.
                <Typing.Backspace count={20} delay={1000} />
                <Typing.Delay ms={200} />
                <Typing.Cursor />
              </h2>
            </Typing>
          </div>
        </div>
        <div className="row my-4 justify-content-center">
          <div className="col-lg-10 card">
            <div className="card-body">
              <div className="card-title">
                <h5>Hi there! 😊</h5>
                <h6>an introduction</h6>
              </div>

              <div className="card-text">
                Hello, I'm <span className="text-highlight">Goldy Mariz</span>.
                Thanks for coming by my website and for taking the time to visit
                around. I think I'll make this page as my autobiography that no
                one is asking for. It's frankly a miracle you're still reading.
                Are you a stalker? Are you bored out of your mind? Are you a
                data mining bot deployed by the <s>reptilian overlord</s> Mark
                Zuckerberg?
                <br />
                <br />I suppose this is the part where I describe myself. I
                solve bugs and write new software features for my job at{" "}
                <a href="https://ncr.com" target="_blank" rel="no-referrer">
                  NCR Corporation
                </a>
                . I am generally a backend developer, although occassionally (by
                force or by free will), I deal with frontend elements. Just like
                right now! I built this website from scratch, from design,
                development, to deployment. This is actually the first time I
                have GatsbyJS, and it has been a breeze despite the learning
                curve. Awesome documentation, blazing fast experience, what more
                could you want! (This is not sponsored I just really like it)
              </div>
            </div>
          </div>
        </div>
        <div className="row my-4 justify-content-center">
          <div className="col-lg-10  card">
            <div className="card-body">
              <div className="card-title">
                <h5>More about me</h5>
                <h6>My taste is all over the place</h6>
              </div>
              <div className="card-text">I love Neon Genesis Evangelion</div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </Layout>
)

export default AboutPage
